@import url(https://cdn.jsdelivr.net/npm/bulma@0.9.2/css/bulma.min.css);
html,
body {
  height: 100%;
}

body {
  font-size: 20px;
  margin: 0;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.center {
  height: 100%;
  padding: 0;
  margin: 0;
  display: -webkit-flex;
  display: flex;
  /* align-items: center; */
}

.box {
  width: 1200px;
}

header {
  margin-top: 2.5em;
  padding-bottom: 1em;
}

main {
  height: 650px;
}

h1,
h2 {
  text-align: center;
}

h2 {
  margin-bottom: 10px;
}

.home-buttons-list {
  margin-top: 1em;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

.home-button {
  width: 450px;
  display: block;
  margin-top: 1em;
  margin-bottom: 1em;
  text-align: left;
  padding-left: 2em;
  font-size: 2rem !important;
}

.done-button {
  width: 280px;
}

#picked-student {
  font-size: 3rem !important;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
}

.reactions-list {
  font-size: 1.75rem;
}

.notify-badge {
  position: absolute;
  padding-top: 7px;
  right: -20px;
  z-index: 10;
  width: 50px;
  height: 50px;
  line-height: 36px;
  background: hsl(48, 100%, 67%);
  text-align: center;
  border-radius: 50%;
  color: black;
  font-size: 1.8rem;
  font-weight: 600;
}

